var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_c('main',{staticClass:"faq",attrs:{"id":"faq"}},[_c('div',{staticClass:"question"},[_c('h3',{staticClass:"question__title",attrs:{"data-toggle":"collapse","data-target":"#q1","aria-expanded":"false","aria-controls":"q1"}},[_vm._v("\n                Что такое мМм?\n            ")]),_vm._v(" "),_c('div',{staticClass:"collapse question__answer",attrs:{"id":"q1"}},[_vm._v("\n                мМм - мобильный модульный майнер с системами иммерсионного охлаждения, жизнеобеспечения и удаленного\n                администрирования.\n                Инфраструктура майнера разработана на базе стандартного грузового контейнера. майнерная вертикальная система\n                компоновки размещаемого оборудования и иммерсионная система охлаждения позволяет разместить до 2 раз больше\n                оборудования в отличие от любых рыночных решений.\n                Система жизнеобеспечения майнера контролирует температуру оборудования и окружающей среды. Она оснащена\n                системами управления электропитанием, контроля доступа и видеонаблюдения, а также системой пожаротушения.\n                Мобильный майнер BiXBiT создан для работы без участия технического персонала - все задачи по настройке и\n                мониторингу процессов решает система удаленного администрирования. Она умеет инвентаризировать все, что\n                находится внутри контейнера и запускать любые типы операционных систем с предустановленными на них\n                драйверами и программами для\n            ")])]),_vm._v(" "),_c('div',{staticClass:"question"},[_c('h3',{staticClass:"question__title",attrs:{"data-toggle":"collapse","data-target":"#q2","aria-expanded":"false","aria-controls":"q2"}},[_vm._v("\n                Что входит в МММ?\n            ")]),_vm._v(" "),_c('div',{staticClass:"collapse question__answer",attrs:{"id":"q2"}},[_c('h4',[_vm._v("МММ включает следующие компоненты и системы:")]),_vm._v(" "),_c('ul',[_c('li',[_vm._v("Контейнер")]),_vm._v(" "),_c('li',[_vm._v("Система стоек и модулей")]),_vm._v(" "),_c('li',[_vm._v("Система иммерсионного охлаждения")]),_vm._v(" "),_c('li',[_vm._v("Система электропитания")]),_vm._v(" "),_c('li',[_vm._v("Телекоммуникационная инфраструктура")]),_vm._v(" "),_c('li',[_vm._v("Система управления и мониторинга")]),_vm._v(" "),_c('li',[_vm._v("Система бездискового запуска")]),_vm._v(" "),_c('li',[_vm._v("Система контроля доступа и охранного видеонаблюдения")]),_vm._v(" "),_c('li',[_vm._v("Система пожаротушения")])])])]),_vm._v(" "),_c('div',{staticClass:"question"},[_c('h3',{staticClass:"question__title",attrs:{"data-toggle":"collapse","data-target":"#q3","aria-expanded":"false","aria-controls":"q3"}},[_vm._v("\n                Сколько оборудования размещается в контейнере?\n            ")]),_vm._v(" "),_c('div',{staticClass:"collapse question__answer",attrs:{"id":"q3"}},[_c('p',[_vm._v("\n                    Оборудование размещается в стандартном грузовом контейнере вместительностью 10, 20 либо 40 футов.\n                    Применяется модульная система компоновки. В один типовой модуль (емкость) можно разместить 8 устройств\n                    ASIC с блоками питания либо 27 видеокарт (размещенных на 3 материнских платах с блоками питания).\n                ")]),_vm._v(" "),_c('ul',[_c('li',[_vm._v("10 футов (~3.05 м) – 8 стоек = 32 модуля = 192 ASIC либо 864 видеокарты;")]),_vm._v(" "),_c('li',[_vm._v("20 футов (~6.10 м) – 16 стоек = 64 модуля = 384 ASIC либо 1728 видеокарт;")]),_vm._v(" "),_c('li',[_vm._v("40 футов (~12.19 м) – 32 стойки = 128 модулей = 768 ASIC либо 3456 видеокарт.")])])])]),_vm._v(" "),_c('div',{staticClass:"question"},[_c('h3',{staticClass:"question__title",attrs:{"data-toggle":"collapse","data-target":"#q4","aria-expanded":"false","aria-controls":"q4"}},[_vm._v("\n                Сколько оборудования размещается в контейнере?\n            ")]),_vm._v(" "),_c('div',{staticClass:"collapse question__answer",attrs:{"id":"q4"}},[_c('p',[_vm._v("\n                    Иммерсионное охлаждение - процесс отвода тепла от работающего оборудования путем его погружения в\n                    ёмкость с иммерсионной жидкостью. Иммерсионная жидкость – это техническая жидкость, имеющая такие\n                    уникальные свойства как:\n                    Негорючесть, низкую токсичность, инертность, высочайшие диэлектрические свойства.\n                    Таким образом, данная жидкость не вступает в химическую реакцию практически ни с чем и является\n                    абсолютным диэлектриком, что позволяет использовать ее в контакте с любым электронным оборудованием без\n                    последствий для него.\n                    Оборудование (устройства ASIC, видеокарты с материнской платой, блоки питания) внутри модуля (емкости)\n                    полностью погружены в теплоноситель, теплопроводность которого в 25 раз выше воздуха, а иммерсионная\n                    система охлаждения обеспечивает равномерный отвод тепла. Но температура не снижается и поддерживается на\n                    уровне 65-75 градусов для видеокарт и 85 градусов для asic. Такая температура оптимальна при утилизации\n                    тепла.\n                ")])])]),_vm._v(" "),_c('div',{staticClass:"question"},[_c('h3',{staticClass:"question__title",attrs:{"data-toggle":"collapse","data-target":"#q5","aria-expanded":"false","aria-controls":"q5"}},[_vm._v("\n                Что включает система управления?\n            ")]),_vm._v(" "),_c('div',{staticClass:"collapse question__answer",attrs:{"id":"q5"}},[_c('p',[_vm._v("\n                    Мобильный майнер BiXBiT укомплектован всей необходимой электротехнической инфраструктурой для\n                    электропитания размещаемого оборудования. Подготовка всего модуля и отдельных емкостей производится под\n                    конкретный набор оборудования заказчика. Для размещения видеокарт модули (емкости) комплектуются\n                    материнскими платами с блоками питания. Для размещения ASIC модули (емкости) могут быть укомплектованы\n                    блоками питания по желанию заказчика.\n                    Система администрирования умеет инвентаризировать все, что находиться внутри контейнера и запускать\n                    любые типы операционных систем с предустановленными на них драйверами и программами для майнинга.\n                    Инфраструктура мМм создавалась для работы без участия системных администраторов и сводила к минимуму\n                    человеческий фактор и возможные ошибки при эксплуатации.\n                    Мобильный майнер BiXBiT комплектуется кабельной системой и активным сетевым оборудованием для\n                    обеспечения доступа в интернет и удаленного управления всем оборудованием. Активное сетевое оборудование\n                    2+ уровня позволяет произвести логическое сегментирование локальной сети. Возможно резервирование\n                    каналов связи.\n                    В комплектацию мобильного модуля BiXBiT входят 2 сервера (основной и резервный) для запуска программного\n                    обеспечения для майнинга и управления системой жизнеобеспечения модуля.\n                    Доступ (в том числе и удаленный) ко всем функциям реализован через веб-портал.\n                    Мобильный майнер BiXBiT укомплектован датчиками и микроконтроллерами, позволяющими осуществлять\n                    мониторинг и управление большим количеством параметров: энергопотребление, температура, уровни\n                    жидкостей, режим работы насосов и т.п.\n                ")])])]),_vm._v(" "),_c('div',{staticClass:"question"},[_c('h3',{staticClass:"question__title",attrs:{"data-toggle":"collapse","data-target":"#q6","aria-expanded":"false","aria-controls":"q6"}},[_vm._v("\n                Как мМм подключается к сети?\n            ")]),_vm._v(" "),_c('div',{staticClass:"collapse question__answer",attrs:{"id":"q6"}},[_c('p',[_vm._v("\n                    Мобильный майнер BiXBiT укомплектован всей необходимой\n                    электротехнической инфраструктурой для\n                    электропитания размещаемого оборудования. Подготовка всего модуля и отдельных емкостей производится под\n                    конкретный набор оборудования заказчика. Для размещения видеокарт модули (емкости) комплектуются\n                    материнскими платами с блоками питания. Для размещения ASIC модули (емкости) могут быть укомплектованы\n                    блоками питания по желанию заказчика.\n                ")])])]),_vm._v(" "),_c('div',{staticClass:"question"},[_c('h3',{staticClass:"question__title",attrs:{"data-toggle":"collapse","data-target":"#q7","aria-expanded":"false","aria-controls":"q7"}},[_vm._v("\n                Как работает система бездискового запуска?\n            ")]),_vm._v(" "),_c('div',{staticClass:"collapse question__answer",attrs:{"id":"q7"}},[_c('p',[_vm._v("\n                    Система бездискового запуска предназначена для управления операционными системами и программным\n                    обеспечением ригов при майнинге с использованием видеокарт. Риг – это материнская плата с блоком\n                    питания, в которую устанавливаются видеокарты.\n                    Система бездискового запуска представляет собой сконфигурированные образы, расположенные в системе\n                    хранения данных (для которой достаточно и одного емкого жесткого диска). Любой из образов можно в любой\n                    момент загрузить на неограниченное количество рабочих машин (ригов), которые уже через 1 минуту начнут\n                    или продолжат работу с новыми настройками.\n                    Система бездискового запуска реализована как дистанционный загрузчик операционных систем на\n                    вычислительных установках. Однажды установленная и полностью сконфигурированная операционная система со\n                    всеми необходимыми драйверами, приложениями для майнинга и привязанными кошельками пользователей\n                    компилируется в образ, который хранится в системе хранения данных (СХД). Этой операционной системой\n                    может быть Windows, Linux или любые их производные (такие как ethOS, Hive и другие). Система\n                    бездискового запуска загружает образы не с жесткого диска вычислительной машины, а из образа\n                    операционной системы в СХД по сети Решение BiXBiT позволяет быстро провести как первичное подключение к\n                    блокчейн-сети, так и последующую смену задач.\n                ")])])]),_vm._v(" "),_c('div',{staticClass:"question"},[_c('h3',{staticClass:"question__title",attrs:{"data-toggle":"collapse","data-target":"#q8","aria-expanded":"false","aria-controls":"q8"}},[_vm._v("\n                Что включает система жизнеобеспечения?\n            ")]),_vm._v(" "),_c('div',{staticClass:"collapse question__answer",attrs:{"id":"q8"}},[_c('p',[_vm._v("\n                    Мобильный майнер BiXBiT укомплектован биометрическими электронными замками, охранными видеокамерами (2\n                    внутри контейнера, 1 обзорная 360° снаружи), датчиками открытия двери и технологических люков.\n                    Все мобильные майнеры BiXBiT укомплектованы газовой системой пожаротушения. Для обнаружения возгорания\n                    используются дымовые датчики и инфракрасные камеры. По желанию заказчика майнер может быть укомплектован\n                    инновационной гибридной системой пожаротушения.\n                ")])])]),_vm._v(" "),_c('div',{staticClass:"question"},[_c('h3',{staticClass:"question__title",attrs:{"data-toggle":"collapse","data-target":"#q9","aria-expanded":"false","aria-controls":"q9"}},[_vm._v("\n                Как осуществляется переключение задач?\n            ")]),_vm._v(" "),_c('div',{staticClass:"collapse question__answer",attrs:{"id":"q9"}},[_c('p',[_vm._v("\n                    Любой из образов можно в любой момент загрузить на неограниченное количество рабочих машин (ригов),\n                    которые уже через 1 минуту начнут или продолжат работу с новыми настройками.\n                ")])])]),_vm._v(" "),_c('div',{staticClass:"question"},[_c('h3',{staticClass:"question__title",attrs:{"data-toggle":"collapse","data-target":"#q10","aria-expanded":"false","aria-controls":"q10"}},[_vm._v("\n                Какое оборудование можно поместить в контейнер?\n            ")]),_vm._v(" "),_c('div',{staticClass:"collapse question__answer",attrs:{"id":"q10"}},[_c('p',[_vm._v("\n                    Любые виды майнингового оборудования от GPU до ASIC. В контейнер можно поместить бу оборудование,\n                    проведя предварительную очистку.\n                ")])])]),_vm._v(" "),_c('div',{staticClass:"question"},[_c('h3',{staticClass:"question__title",attrs:{"data-toggle":"collapse","data-target":"#q11","aria-expanded":"false","aria-controls":"q11"}},[_vm._v("\n                За счет чего увеличивается производительность оборудования?\n            ")]),_vm._v(" "),_c('div',{staticClass:"collapse question__answer",attrs:{"id":"q11"}},[_c('p',[_vm._v("\n                    Эффективное иммерсионное охлаждение позволяет повысить производительность майнингового оборудования на\n                    20-40% за счет увеличение рабочих частот при сохранении термостабильности и прежнего уровня\n                    электропотребления.\n                ")])])]),_vm._v(" "),_c('div',{staticClass:"question"},[_c('h3',{staticClass:"question__title",attrs:{"data-toggle":"collapse","data-target":"#q12","aria-expanded":"false","aria-controls":"q12"}},[_vm._v("\n                Как мМм снижает электропотребление?\n            ")]),_vm._v(" "),_c('div',{staticClass:"collapse question__answer",attrs:{"id":"q12"}},[_c('p',[_vm._v("\n                    При иммерсионном охлаждении устройствам больше не нужны собственные кулеры, за счет чего снижается\n                